import axios from 'axios'
import service from './api'
let root = process.env.url_api // process.env.url_api ''
// import { Indicator, Toast } from 'mint-ui'

// service循环遍历输出不同的请求方法
let instance = axios.create({
  baseURL: root, // http://menkouapp.sun7y.com示例 http://117.78.28.26 http://lcg.momentinlet.com https://www.momentinlet.com http://t.momentinlet.com https://www.xcacc.net/
  timeout: 0
})
let instance2 = axios.create({
  baseURL: 'http://221.226.82.226:8003', // http://menkouapp.sun7y.com示例
  timeout: 0
})
const Http = {} // 包裹请求方法的容器

// 请求格式/参数的统一
for (let key in service) {
  let api = service[key] // url method
  // async的作用：避免进入回调地狱
  Http[key] = async function (
    params, // 请求参数 get,delete:url。put,post,patch:data。
    isFormData = false, // 标识是否是form-data请求
    config = {} // 配置参数
  ) {
    let url = api.url
    let newParams = {}
    // 判断是否需要加载loading框
    if (api.mask) {
    } else {
    }
    // content-type是否是form-data的判断
    let myIds = null // post请求时isFormData为true的情况下，如需要info/1123这种接口，则给myIds赋值，参数名为ids
    if (params && isFormData) {
      newParams = new FormData()
      for (let i in params) {
        if (api.detail && i === 'ids') {
          myIds = params.ids
        } else {
          newParams.append(i, params[i])
        }
      }
    } else {
      newParams = params
    }
    if (api.detail && newParams.ids) {
      // 该情况为详情接口直接在url上带参，如：info/1123。该情况需在api.js里对应接口加detail: true，
      url = url + newParams.ids
      delete newParams.ids
    } else if (api.detail && myIds) {
      url = url + myIds
    }
    // 不同请求的判断
    let response = {} // 请求的返回值
    if (api.method === 'put' || api.method === 'post' || api.method === 'patch') {
      try {
        if (api.ins && api.ins === 'instance2') {
          response = await instance2[api.method](url, newParams, config)
        } else {
          response = await instance[api.method](url, newParams, config)
        }
      } catch (err) {
        response = err
      }
    } else if (api.method === 'delete' || api.method === 'get') {
      config.params = newParams // 从url上传参。如果要放在data里，就在config里设个data。
      try {
        if (api.ins && api.ins === 'instance2') {
          response = await instance2[api.method](url, config)
        } else {
          response = await instance[api.method](url, config)
        }
      } catch (err) {
        response = err
      }
    }
    return response // 返回响应值
  }
}

// 拦截器的添加
// 请求拦截器
instance.interceptors.request.use(config => {
  // 发起请求前做些什么
  config.headers.token = sessionStorage.token
  return config
}, () => {
  // 请求错误
  // Indicator.close()
  // Toast('请求错误，请稍后重试~')
})
// 响应拦截器
instance.interceptors.response.use(res => {
  // 请求成功
  // Indicator.close()
  console.log(res.data)
  // 登录出错重新登录
  if (res.data && res.data.code && res.data.code === 401) {
    localStorage.removeItem('shopId')
    localStorage.removeItem('token')
    // window.location.replace('/#/login')
    window.location.replace('/login')
  }
  return res.data
}, () => {
  // 请求失败
  // Indicator.close()
  // Toast('请求错误，请稍后重试~')
})

// 拦截器的添加
// 请求拦截器
instance2.interceptors.request.use(config => {
  // 发起请求前做些什么
  return config
}, () => {
  // 请求错误
  // Indicator.close()
  // Toast('请求错误，请稍后重试~')
})
// 响应拦截器
instance2.interceptors.response.use(res => {
  // 请求成功
  // Indicator.close()
  return res.data
}, () => {
  // 请求失败
  // Indicator.close()
  // Toast('请求错误，请稍后重试~')
})

export default Http



