<template>
  <a-locale-provider :locale="locale">
  <div id="app">
    <router-view/>
  </div>
  </a-locale-provider>
</template>

<script>
import zhCN from 'ant-design-vue/lib/locale-provider/zh_CN'
export default {
  name: 'App',
  data () {
    return {
      locale: zhCN
    }
  }
}
</script>

<style>
  body, html {
    width: 100%;
  }
  #app {
    width: 100%;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  .ant-layout-content {
    min-height: auto;
  }
</style>



